export default {
  data() {
    return {
      input: "",
      options: [
        {
          value: "选项1",
          label: "已读"
        },
        {
          value: "选项2",
          label: "未读"
        },
        {
          value: "选项3",
          label: "已经领取"
        },
        {
          value: "选项4",
          label: "未领取"
        },
        {
          value: "选项5",
          label: "待发送"
        }
      ],
      tableData: [
        {
          topuptype: "充值类型",
          maxamount: "自定义最大金额",
          minamount: "可自定义最小金额",
          paynode: "支付备注",
          effect: "是否生效",
          recommend: "是否推荐",
          operation: "操作者",
          operationtime: "操作时间"
        }
      ],
      value: "",
      currentPage4: 1,
      visiblity: false,
      rules: {
        common: [{ required: true, message: "充值金额不合法", trigger: "blur" }]
      },
      form: {},
      title: "记录",
      disabled: false
    };
  },
  methods: {
    writeEmail() {
      this.init("记录", false);
    },
    search() {},
    handleEdit() {
      this.init("编辑", false);
    },
    detail() {
      (this.rules = {}), this.init("邮件详情", true);
    },
    send() {},
    init(title, disabled) {
      this.visiblity = true;
      this.disabled = disabled;
      this.title = title;
    },
    handleSizeChange() {},
    handleCurrentChange() {},
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$message({
            message: "修改成功",
            type: "success"
          });

        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};



